
@color-blue: #1652F0;
@color-red: #ff7f00;
@color-green: #4fbb92;

@color-blue: #1968FC;
@color-nav: #202B50;

body {
    background: linear-gradient(to left, #EEF4FF, #F4F6FF);
}

input {
    height: 48px;
    line-height: 48px;
    background-color: #fff;
    border:1px solid rgba(215,223,240,1);
    padding: 0 12px;
    &:hover {
        border-color: @color-blue;
    }
}

a:link {
    color: @color-blue;
    transition: .4s all ease;
    &:hover {
        color: #237DFC;
    }
}

.ui-btn {
    display: inline-block;
    width: 160px;
    height: 48px;
    line-height: 48px;
    background-color: @color-blue;
    color: #fff !important;
    text-align: center;
    border-radius: 6px;
    &:hover {
        background-color: #237DFC;
    }
    &[disabled="disabled"] {
        pointer-events: none;
        opacity: .5;
    }
}

.ui-title {
    position: relative;
    margin-bottom: 50px;
    font-size: 32px;
    padding-bottom: 10px;
    color: #33323C;
    &:after {
        position: absolute;
        content: "";
        left: 0;
        bottom: 0;
        border: 4px solid @color-blue;
        width: 48px;

    }
    &-white {
        color: #fff;
    }
    @media only screen and (max-width: 414px) {
        //padding-left: 1rem;
        margin-bottom: 20px;
        font-size: 24px;
        text-align: center;
        &:after {
            left: 50%;
            transform: translate(-50%, 0);
        }
    }
}

.ui-title-join {
    position: relative;
    width: 436px;
    height: 36px;
    margin: 0 auto 28px;
    background-color: #E0E7F5;
    text-align: center;
    font-size: 24px;
    white-space: nowrap;
    font-weight:200;
    color:rgba(102,101,113,1);
    .text {
        position: absolute;
        top: -16px;
        left: 50%;
        transform: translate(-50%, 0);
    }
    @media only screen and (max-width: 414px) {
        //margin-bottom: 16px;
        width: 220px;
        height: 18px;
        font-size: 18px;
        white-space: normal;
        .text {
            width: 90%;
            top: 50%;
            transform: translate(-50%, -50%);
        }
    }
}

.bg-sanjiao {
    position: absolute;
    top: 0;
    left: 0;
    width: 0;
    height: 0;
    border-top: 800px solid @color-nav;
    border-right: 1000px solid transparent;
}

.wrapper {
    width: 1120px;
    margin: 0 auto;
    @media only screen and (max-width: 414px) {
        width: 100%;
    }
}

.box {
    margin-bottom: 80px;
}

.form {
    display: flex;
    justify-content: space-between;
    .form-col {
        display: flex;
        align-items: center;
    }
    .label {
        margin-right: 16px;
        color: #0D111F;
    }
    .input {
        width: 450px;
    }
    .ui-btn {
        width: 240px;
    }
    @media only screen and (max-width: 414px) {
        display: block;
        .form-col {
            margin-bottom: 32px;
        }
        .input {
            width: calc(100% - 40px);
        }
        .ui-btn {
            width: 160px;
        }
    }
}

.event-container {
    background: url("../../images/event/banner-bg.png") 0 0 no-repeat;
    background-size: 100% 1600px;
    color: #fff;

    .logo {
        margin-left: 0!important;
    }

    .nav .nav-opt {
        color: #fff;
        &:hover {

        }
    }
    .nav .navigation {
        margin-right: 0;
    }

    @media only screen and (max-width: 414px) {
        & {
            //background-image: none;
            background-size: 100% 1600px;
        }
        .nav-mobile {
            position: static;
            display: flex;
            justify-content: space-between;
            padding: 0 .4rem;
            background-color: inherit;
            >.ui-btn {
                width: auto;
                padding: 0 12px;
                height: 36px;
                line-height: 36px;
                font-size: 12px;
            }
        }

    }
}

.banner {
    display: flex;
    justify-content: space-between;
    align-items: center;
    color: #fff;
    .banner-left {
        flex: 1;
        margin-right: 40px;
        .title {
            font-size: 50px;
            margin-bottom: 16px;
        }
        .title-sub {
            position: relative;
            //width: 390px;
            height: 36px;
            border-radius: 36px;
            margin-bottom: 28px;
            background-color: #41466c;
            font-size: 36px;
            white-space: nowrap;
            font-weight:200;
            color: #fff;
            .text {
                position: absolute;
                left: 0;
                top: 50%;
                transform: translate(0, -50%);
            }
            &:after {

            }
        }
        .title-slogen {
            margin-top: 8px;
            font-size: 22px;
            opacity: .5;
        }
    }
    .banner-right {
        .img {
            width: 600px;
            height: 500px;
        }
    }
    @media only screen and (max-width: 414px) {
        & {
            padding: 40px 0 100px 0;
            //background-color: @color-nav;
        }
        .banner-left {
            flex: 1;
            margin-right: 0;
            padding-top: 60px;
            text-align: center;
            .title {
                font-size: 30px;
            }
            .title-sub {
                width: 280px;
                margin: 0 auto 20px;
                font-size: 22px;
                .text {
                    left: 50%;
                    transform: translate(-50%, -50%);
                }
            }
            .title-slogen {
                font-size: 18px;
            }
        }
        .banner-right {
            display: none;
        }
    }
}

.story {
    color: #fff;
    .story-content {
        background-color: rgba(32, 43, 80, .75);
        padding: 48px;
        .list {
            display: flex;
            justify-content: space-between;
            .item {
                flex: 1;
                margin-right: 100px;
                &:last-child {
                    margin-right: 0;
                }
            }
            .title {
                margin-bottom: 16px;
                font-size: 24px;
            }
            .detail {
                font-size: 16px;
            }
        }
    }
    @media only screen and (max-width: 414px) {
        & {
            //background-color: @color-nav;
        }
        .story-content {
            padding: 2rem 1rem;
            .list {
                display: block;
                .item {
                    margin-bottom: 20px;
                    margin-right: 0;
                    .title {

                    }
                }
            }
        }
    }
}

.for-me {
    .form-me-content {
        .list {
            display: flex;
            .item {
                flex: 1;
                margin-right: 40px;
                &:last-child {
                    margin-right: 0;
                }
                .img {
                    width: 100%;
                }
            }
        }
    }
    @media only screen and (max-width: 414px) {
        .form-me-content {
            .list {
                display: block;
                padding: 0 1rem;
                .item {
                    margin-right: 0;
                    margin-bottom: 1rem;
                }
            }
        }
    }
}

.join-us {
    .join-us-guide {
        color: #666571;
        .guide-first {

        }
        .guide-second {
            margin-top: 28px;
            margin-bottom: 32px;
            font-size: 22px;
            text-align: center;
            .slide {
                margin-top: 8px;
                .icon {
                    display: inline-block;
                    color: #666571;
                }
            }
        }
    }
    .join-us-content {
        margin-top: 28px;
        .list {
            display: flex;
            .item {
                display: flex;
                flex-direction: column;
                flex: 1;
                margin-right: 100px;
                &:last-child {
                    margin-right: 0;
                }
                .item-number {
                    margin-bottom: 24px;
                    font-size: 36px;
                    color: @color-blue;
                    text-align: center;
                }
                .item-box {
                    box-shadow: 1px 6px 32px 6px rgba(96,141,225,0.1);
                }
                .item-header {
                    display: flex;
                    align-items: center;
                    justify-content: center;
                    margin-bottom: 24px;
                    font-size: 20px;
                    height: 88px;
                    .img-full {
                        display: block;
                        width: 100%;
                        height: 100%;
                    }
                    .img-logo {
                        height: 48px;
                    }
                    background-color: #000;
                }
                .item-body, .item-footer {
                    padding: 0 20px;
                }
                .item-body {
                    flex: 1;
                    min-height: 180px;
                    color: #33323C;
                    line-height: 1.6;
                    .body-title {
                        font-size: 20px;
                        margin-bottom: 12px;
                    }
                }
                .item-footer {
                    margin: 24px 0;
                    text-align: center;
                    .item-link {
                        color: @color-blue;
                        transition: .4s all ease;
                        &:hover {
                            color: #237DFC;
                        }
                    }
                }
            }
        }
    }
    @media only screen and (max-width: 414px) {
        .ui-title {
            margin-bottom: 32px;
        }
        .join-us-guide {
            .guide-first {

            }
            .guide-second {
                font-size: 18px;
                margin-top: 20px;
            }
        }
        .join-us-content {
            .list {
                display: block;
                padding: 0 1rem;
                .item {
                    margin-right: 0;
                    margin-bottom: 20px;
                    .item-number {
                        margin-bottom: 4px;
                        text-align: left;
                        font-size: 28px;
                    }
                }
            }
        }

    }
}

.go {
    margin-top: 120px;
    .go-form {
        .form-submit {
            margin-top: 48px;
            text-align: center;
        }
    }
    @media only screen and (max-width: 414px) {
        .go-form {
            margin-top: 60px;
            padding: 0 1rem;
        }
    }
}

.download {
    background-color: #1A2446;
    padding: 140px 0;
    text-align: center;
    .title {
        margin-bottom: 16px;
        font-size: 32px;
        color: #fff;
    }
    .title-sub {
        margin-bottom: 40px;
        font-size: 18px;
        color: #fff;
        opacity: .3;
    }
    .btn-group {
        text-align: center;
        .download-btn {
            display: inline-block;
            margin-right: 32px;
            &:last-child {
                margin-right: 0;
            }
            .link {
                display: block;
            }
        }
    }
    @media only screen and (max-width: 414px) {
        padding: 60px 0;
        .title {
            font-size: 24px;
        }
        .title-sub {
            margin-bottom: 24px;
        }
        .btn-group {
            .download-btn {
                margin-right: 1rem;
                .link {
                    img {
                        height: 48px;
                    }
                }
            }
        }
    }
}


