body {
  background: linear-gradient(to left, #EEF4FF, #F4F6FF);
}
input {
  height: 48px;
  line-height: 48px;
  background-color: #fff;
  border: 1px solid #d7dff0;
  padding: 0 12px;
}
input:hover {
  border-color: #1968FC;
}
a:link {
  color: #1968FC;
  transition: 0.4s all ease;
}
a:link:hover {
  color: #237DFC;
}
.ui-btn {
  display: inline-block;
  width: 160px;
  height: 48px;
  line-height: 48px;
  background-color: #1968FC;
  color: #fff !important;
  text-align: center;
  border-radius: 6px;
}
.ui-btn:hover {
  background-color: #237DFC;
}
.ui-btn[disabled="disabled"] {
  pointer-events: none;
  opacity: 0.5;
}
.ui-title {
  position: relative;
  margin-bottom: 50px;
  font-size: 32px;
  padding-bottom: 10px;
  color: #33323C;
}
.ui-title:after {
  position: absolute;
  content: "";
  left: 0;
  bottom: 0;
  border: 4px solid #1968FC;
  width: 48px;
}
.ui-title-white {
  color: #fff;
}
@media only screen and (max-width: 414px) {
  .ui-title {
    margin-bottom: 20px;
    font-size: 24px;
    text-align: center;
  }
  .ui-title:after {
    left: 50%;
    transform: translate(-50%, 0);
  }
}
.ui-title-join {
  position: relative;
  width: 436px;
  height: 36px;
  margin: 0 auto 28px;
  background-color: #E0E7F5;
  text-align: center;
  font-size: 24px;
  white-space: nowrap;
  font-weight: 200;
  color: #666571;
}
.ui-title-join .text {
  position: absolute;
  top: -16px;
  left: 50%;
  transform: translate(-50%, 0);
}
@media only screen and (max-width: 414px) {
  .ui-title-join {
    width: 220px;
    height: 18px;
    font-size: 18px;
    white-space: normal;
  }
  .ui-title-join .text {
    width: 90%;
    top: 50%;
    transform: translate(-50%, -50%);
  }
}
.bg-sanjiao {
  position: absolute;
  top: 0;
  left: 0;
  width: 0;
  height: 0;
  border-top: 800px solid #202B50;
  border-right: 1000px solid transparent;
}
.wrapper {
  width: 1120px;
  margin: 0 auto;
}
@media only screen and (max-width: 414px) {
  .wrapper {
    width: 100%;
  }
}
.box {
  margin-bottom: 80px;
}
.form {
  display: flex;
  justify-content: space-between;
}
.form .form-col {
  display: flex;
  align-items: center;
}
.form .label {
  margin-right: 16px;
  color: #0D111F;
}
.form .input {
  width: 450px;
}
.form .ui-btn {
  width: 240px;
}
@media only screen and (max-width: 414px) {
  .form {
    display: block;
  }
  .form .form-col {
    margin-bottom: 32px;
  }
  .form .input {
    width: calc(100% - 40px);
  }
  .form .ui-btn {
    width: 160px;
  }
}
.event-container {
  background: url("../../images/event/banner-bg.png") 0 0 no-repeat;
  background-size: 100% 1600px;
  color: #fff;
}
.event-container .logo {
  margin-left: 0!important;
}
.event-container .nav .nav-opt {
  color: #fff;
}
.event-container .nav .navigation {
  margin-right: 0;
}
@media only screen and (max-width: 414px) {
  .event-container {
    background-size: 100% 1600px;
  }
  .event-container .nav-mobile {
    position: static;
    display: flex;
    justify-content: space-between;
    padding: 0 0.4rem;
    background-color: inherit;
  }
  .event-container .nav-mobile > .ui-btn {
    width: auto;
    padding: 0 12px;
    height: 36px;
    line-height: 36px;
    font-size: 12px;
  }
}
.banner {
  display: flex;
  justify-content: space-between;
  align-items: center;
  color: #fff;
}
.banner .banner-left {
  flex: 1;
  margin-right: 40px;
}
.banner .banner-left .title {
  font-size: 50px;
  margin-bottom: 16px;
}
.banner .banner-left .title-sub {
  position: relative;
  height: 36px;
  border-radius: 36px;
  margin-bottom: 28px;
  background-color: #41466c;
  font-size: 36px;
  white-space: nowrap;
  font-weight: 200;
  color: #fff;
}
.banner .banner-left .title-sub .text {
  position: absolute;
  left: 0;
  top: 50%;
  transform: translate(0, -50%);
}
.banner .banner-left .title-slogen {
  margin-top: 8px;
  font-size: 22px;
  opacity: 0.5;
}
.banner .banner-right .img {
  width: 600px;
  height: 500px;
}
@media only screen and (max-width: 414px) {
  .banner {
    padding: 40px 0 100px 0;
  }
  .banner .banner-left {
    flex: 1;
    margin-right: 0;
    padding-top: 60px;
    text-align: center;
  }
  .banner .banner-left .title {
    font-size: 30px;
  }
  .banner .banner-left .title-sub {
    width: 280px;
    margin: 0 auto 20px;
    font-size: 22px;
  }
  .banner .banner-left .title-sub .text {
    left: 50%;
    transform: translate(-50%, -50%);
  }
  .banner .banner-left .title-slogen {
    font-size: 18px;
  }
  .banner .banner-right {
    display: none;
  }
}
.story {
  color: #fff;
}
.story .story-content {
  background-color: rgba(32, 43, 80, 0.75);
  padding: 48px;
}
.story .story-content .list {
  display: flex;
  justify-content: space-between;
}
.story .story-content .list .item {
  flex: 1;
  margin-right: 100px;
}
.story .story-content .list .item:last-child {
  margin-right: 0;
}
.story .story-content .list .title {
  margin-bottom: 16px;
  font-size: 24px;
}
.story .story-content .list .detail {
  font-size: 16px;
}
@media only screen and (max-width: 414px) {
  .story .story-content {
    padding: 2rem 1rem;
  }
  .story .story-content .list {
    display: block;
  }
  .story .story-content .list .item {
    margin-bottom: 20px;
    margin-right: 0;
  }
}
.for-me .form-me-content .list {
  display: flex;
}
.for-me .form-me-content .list .item {
  flex: 1;
  margin-right: 40px;
}
.for-me .form-me-content .list .item:last-child {
  margin-right: 0;
}
.for-me .form-me-content .list .item .img {
  width: 100%;
}
@media only screen and (max-width: 414px) {
  .for-me .form-me-content .list {
    display: block;
    padding: 0 1rem;
  }
  .for-me .form-me-content .list .item {
    margin-right: 0;
    margin-bottom: 1rem;
  }
}
.join-us .join-us-guide {
  color: #666571;
}
.join-us .join-us-guide .guide-second {
  margin-top: 28px;
  margin-bottom: 32px;
  font-size: 22px;
  text-align: center;
}
.join-us .join-us-guide .guide-second .slide {
  margin-top: 8px;
}
.join-us .join-us-guide .guide-second .slide .icon {
  display: inline-block;
  color: #666571;
}
.join-us .join-us-content {
  margin-top: 28px;
}
.join-us .join-us-content .list {
  display: flex;
}
.join-us .join-us-content .list .item {
  display: flex;
  flex-direction: column;
  flex: 1;
  margin-right: 100px;
}
.join-us .join-us-content .list .item:last-child {
  margin-right: 0;
}
.join-us .join-us-content .list .item .item-number {
  margin-bottom: 24px;
  font-size: 36px;
  color: #1968FC;
  text-align: center;
}
.join-us .join-us-content .list .item .item-box {
  box-shadow: 1px 6px 32px 6px rgba(96, 141, 225, 0.1);
}
.join-us .join-us-content .list .item .item-header {
  display: flex;
  align-items: center;
  justify-content: center;
  margin-bottom: 24px;
  font-size: 20px;
  height: 88px;
  background-color: #000;
}
.join-us .join-us-content .list .item .item-header .img-full {
  display: block;
  width: 100%;
  height: 100%;
}
.join-us .join-us-content .list .item .item-header .img-logo {
  height: 48px;
}
.join-us .join-us-content .list .item .item-body,
.join-us .join-us-content .list .item .item-footer {
  padding: 0 20px;
}
.join-us .join-us-content .list .item .item-body {
  flex: 1;
  min-height: 180px;
  color: #33323C;
  line-height: 1.6;
}
.join-us .join-us-content .list .item .item-body .body-title {
  font-size: 20px;
  margin-bottom: 12px;
}
.join-us .join-us-content .list .item .item-footer {
  margin: 24px 0;
  text-align: center;
}
.join-us .join-us-content .list .item .item-footer .item-link {
  color: #1968FC;
  transition: 0.4s all ease;
}
.join-us .join-us-content .list .item .item-footer .item-link:hover {
  color: #237DFC;
}
@media only screen and (max-width: 414px) {
  .join-us .ui-title {
    margin-bottom: 32px;
  }
  .join-us .join-us-guide .guide-second {
    font-size: 18px;
    margin-top: 20px;
  }
  .join-us .join-us-content .list {
    display: block;
    padding: 0 1rem;
  }
  .join-us .join-us-content .list .item {
    margin-right: 0;
    margin-bottom: 20px;
  }
  .join-us .join-us-content .list .item .item-number {
    margin-bottom: 4px;
    text-align: left;
    font-size: 28px;
  }
}
.go {
  margin-top: 120px;
}
.go .go-form .form-submit {
  margin-top: 48px;
  text-align: center;
}
@media only screen and (max-width: 414px) {
  .go .go-form {
    margin-top: 60px;
    padding: 0 1rem;
  }
}
.download {
  background-color: #1A2446;
  padding: 140px 0;
  text-align: center;
}
.download .title {
  margin-bottom: 16px;
  font-size: 32px;
  color: #fff;
}
.download .title-sub {
  margin-bottom: 40px;
  font-size: 18px;
  color: #fff;
  opacity: 0.3;
}
.download .btn-group {
  text-align: center;
}
.download .btn-group .download-btn {
  display: inline-block;
  margin-right: 32px;
}
.download .btn-group .download-btn:last-child {
  margin-right: 0;
}
.download .btn-group .download-btn .link {
  display: block;
}
@media only screen and (max-width: 414px) {
  .download {
    padding: 60px 0;
  }
  .download .title {
    font-size: 24px;
  }
  .download .title-sub {
    margin-bottom: 24px;
  }
  .download .btn-group .download-btn {
    margin-right: 1rem;
  }
  .download .btn-group .download-btn .link img {
    height: 48px;
  }
}
